<template>
  <div class="test" id="test">
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item label="理由(非必需)">
        <el-select v-model="form.reason" placeholder="请选择删除理由">
          <el-option
            v-for="item in violationTypes"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="loading">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import SystemService from '@/service/systemService'
  export default {
    name: 'test',
    props: ['formSubmit','popData'],
    data() {
      return {
        loading:false,
        violationTypes:[], //删除理由选项列表
        form:{
          reason:'',
        }
      }
    },
    created(){
      this.getViolationTypes()
    },
    methods:{
      getViolationTypes() {
        let self = this
//        SystemService.test({}, this, (res) => {
//          let data = res.body.data.result
//          if (data.status == 0) {
//          }
//        })

      },
      onSubmit(){
        this.form.id=this.popData.id
        this.loading=true
        SystemService.test(this.form, this, (res) => {
          let data = res.body
          if(data.status===0){
            this.$message({
              type: 'success',
              message: '操作成功!'
            });
            this.formSubmit()
          } else {
            this.$message({
              type: 'erroe',
              message: '操作失败!'
            });
          }
          this.loading=false
        })
      },
    }
  }
</script>
<style>
  .test .el-form-item {
    margin-bottom: 20px;
  }
</style>




